/** @format */

import classes from "./BusinessData.module.css";
import React, { useEffect } from "react";
import { useState } from "react";
import { connect } from "react-redux";
import BackBtn from "../../Components/UI/BackBtn/BackBtn";
import { getBusinessStatus } from "../../api/Business";
import * as actions from "../../Store/actions/index";
import UIButton from "../../Components/UI/UIButton/UIButton";

const BusinessData = (props) => {
  const [data, setData] = useState(null);

  useEffect(() => {
    props.startLoading();
    getBusinessStatus()
      .then((res) => {
        setData(res);
        props.cancelLoading();
      })
      .catch((err) => props.error(err));
    if (props.authStep !== 15) {
      props.authSteps(15);
    }
  }, []);

  return (
    <section className={classes.BusinessData}>
      <BackBtn />
      <div>
        <h1>
          Welcome back
          <br /> {data && data.name}
        </h1>
        <p>
          You have to setup your business and social profiles to use the
          easysocial platform.
        </p>
        <UIButton
          svgStyle={{ paddingLeft: "4%" }}
          className={classes.Btn}
          onClick={() => {
            if (data.id && data.step) {
              sessionStorage.setItem("businessId", data.id);
              props.authSteps(Number(data.step));
            }
          }}
        >
          Continue account setup
        </UIButton>
      </div>
    </section>
  );
};

const mapStateToProps = (state) => {
  return {
    authStep: state.auth.currentCount,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    authSteps: (currentCount) => dispatch(actions.authSteps(currentCount)),
    startLoading: () => dispatch(actions.startLoading()),
    cancelLoading: () => dispatch(actions.cancelLoading()),
    error: () => dispatch(actions.error()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BusinessData);
